import React, { PureComponent, Fragment } from 'react';
import {connect} from 'react-redux';
import folderCss from './folder.scss';
import classNames from 'classnames';
// import * as actions from '../../actions';
import SubWindow from '../subwindow';

const scss = classNames.bind(folderCss);
class Folder extends PureComponent {

    handleClickEvt = (value,index) => {
        switch (value) {
            case 'tistory':
                return window.open('http://backback.tistory.com/');
            case 'github':
                return window.open('https://github.com/FactAttacker?tab=repositories/');    
            default:
                this.openModal(index);
                break;
        }
    }

    openModal = (index) =>{        
        this.props.folderOpen(index, this.props.folderSelected(index));
        // console.log(this.props.folderOpen);
        // const folder = this.state.folder.map( (key,ind) => {
        //     if(ind === index)
        //         return {...key, open:true, z_index:10}
        //     else
        //         return {...key, z_index:1}
        // });

        // console.log("chosenFolder : ", folder );
        // this.setState({
        //     folder:folder
        // });
    }

    // closeModal = (index) =>{
    //     // const folder = this.state.folder.map( (key,ind) => {
    //     //     if(ind === index)
    //     //         return {...key, open:false, z_index:1}
    //     //     else
    //     //         return {...key}
    //     // });
    //     // this.setState({
    //     //     folder:folder
    //     // });
    //     this.props.folderClose(index);
    // }

    handleShowFolder = () =>{
        // const folders = this.state.folder;
        const folders = this.props.redux_folder.folder;
        const liList = (data,index) => {
            return(
                <li key={'folder-list-'+index}>
                    <div className={scss('folder')}
                        onClick={()=>{this.handleClickEvt(data.name,index)}}>
                        <img src={data.imgUrl} alt={data.text}/>
                        <div className={scss('text')}>
                            <span>{data.text}</span>
                        </div>
                    </div>
                </li>
            );
        }

        const ulGroup = (rowData,index) =>(
            <ul className="folder_row" 
                key={'folder-group-'+index}> {rowData} </ul>
        );
            
        let arrayList = [];
        let rowData   = [];
        let arrayNum  = folders.length;
        for(let i =0 ; arrayNum > i ; i++){
            if( i !==0 && i%3 === 0){ // 행 갯수 
                // console.log(" folder :"+ folders[i].name);
                arrayList.push(ulGroup(rowData,i));
                rowData = [];
            }
            rowData.push(liList(folders[i],i)); 
            if(arrayNum === (i+1)) arrayList.push(ulGroup(rowData,(i+1)));
        };
        return arrayList;
    }
    
    render(){
        // console.log("modal state : ", this.state.folder);
        // console.log("modal props : ", this.props);
        // console.log("modal props.redux_folder : ", this.props.redux_folder);
        return(
            <Fragment>
                <div id="content">
                    <div className="desktop_bg fade" />
                    <div className="desktop">
                        {this.handleShowFolder()}
                    </div>
                </div>
                <div>
                    {this.props.redux_folder.folder.map( (element,idx) => {
                        if( element.open ){
                            return <SubWindow key={ 'window-'+idx } subIndex={idx} chosenFolder={ element }/>
                        }
                        return null;
                    })}
                </div>
            </Fragment>
        )
    }

}

function mapStateToProps(state){
    return state;
}

// const mapToDispatch = (dispatch) =>({
//     folderOpen    : (index) => dispatch(actions.folderOpen(index)),
//     folderSelected: (index) => dispatch(actions.folderSelected(index)),
//     folderClose   : (index) => dispatch(actions.folderClose(index)),
// });

// export default connect(mapStateToProps,mapToDispatch)(Folder);
export default connect(mapStateToProps)(Folder);