import React, { PureComponent } from 'react';
import {connect} from 'react-redux';
import DraggableCore from 'react-draggable';
import styled from 'styled-components';
import * as actions from '../../actions';
import './subWindow.scss';
import {aboutMe,career,study} from '../submenulist/aboutMe';

class subWindow extends PureComponent{
    
    constructor(props){
        super(props);
        this.state = {
            activeDrags:0,
            contentCode:'',
            modalSizeClass: 'pop_maxsize_on'
        }
    }

   
    onStart = () => {
        this.setState({...this.state , activeDrags: 1});
    }
    
    onStop = () => {
        this.setState({...this.state, activeDrags: 0});
    } 


    /*창 버튼 */
    selected = (index) => {
        console.log("#@@@2");
        if(this.closeYN !== 'Y')
            this.props.folderSelected(index);
        this.closeYN = '';
    }
    close = (index) => {
        this.closeYN = 'Y';
        this.props.folderClose(index);
    }

    /*스타일 */
    Subtitle = styled.li`
        padding:0.5rem 0 0.2rem;
        span{
            font-weight: bold;
        }
    `;

    aboutMeContentDOM = {
        AM : (<ul>
                <this.Subtitle>
                    <span>이름 : </span>{aboutMe.subMenu[0].name}
                </this.Subtitle>
                <this.Subtitle>
                    <span>생년월일 : </span>{aboutMe.subMenu[0].birthDay}
                </this.Subtitle>
                <this.Subtitle>
                    <span>학력 : </span>{aboutMe.subMenu[0].birthDay}
                </this.Subtitle>
                <this.Subtitle>
                    {aboutMe.subMenu[0].text}
                </this.Subtitle>
            </ul>)
        ,CA : career.subMenu.map((key) => { 
                return <ul>
                            <this.Subtitle>
                                <span>{key.date}</span>
                            </this.Subtitle>
                            <li>
                                <span>회사 : </span>{key.company}
                            </li>
                            <li>
                                <span>직종: </span>{key.part}
                            </li>
                            <li>
                                <span>업무: </span>{key.text}
                            </li>
                            <li>
                                <span>기술: </span>{key.skill}
                            </li>
                        </ul>
                })
        ,ST :  study.subMenu.map((key) => { 
                return <ul>
                            <this.Subtitle>
                                <span>{key.date}</span>
                            </this.Subtitle>
                            <li>
                                <span>회사 : </span>{key.company}
                            </li>
                            <li>
                                <span>과정명: </span>{key.title}
                            </li>
                            <li>
                                <span>기술: </span>{key.skill}
                            </li>
                        </ul>
                })        
    }

    aboutMeContent = (code) => {
        switch (code) {
            case 'AM':
                return this.aboutMeContentDOM.AM;
            case 'CA':
                return this.aboutMeContentDOM.CA;
            
            case 'ST':
                return this.aboutMeContentDOM.ST;    
                
            default: 
                return this.aboutMeContentDOM.AM;
        }
    }

    domContent = (name) => {
        switch (name) {
            case 'user-home':
                console.log("aboutMe : ",aboutMe.subMenu);
                return ( 
                        <div className="pop_content">
                            <div className="">
                            <ul>
                                <li></li>
                                <li></li>
                            </ul>
                            </div>
                            <div className="pop_preview">
                                <div className="menu-list">
                                    <ul>
                                        <li className="user-menu-1" onClick={() => this.setState({ ...this.state, contentCode: aboutMe.code}) } >
                                            {aboutMe.name}
                                        </li>
                                        <li className="user-menu-2" onClick={() => this.setState({ ...this.state, contentCode: career.code}) }>
                                            {career.name}   
                                        </li>
                                        <li className="user-menu-3" onClick={() => this.setState({ ...this.state, contentCode: study.code}) }>
                                            {study.name}
                                        </li>
                                    </ul>
                                </div>
                                <div className="sub-content">
                                    {this.aboutMeContent(this.state.contentCode)}
                                </div>
                            </div>
                        </div>
                        );
        
            default:
                return ( <div className="pop_content">
                            <div className="">
                            <ul>
                                <li></li>
                                <li></li>
                            </ul>
                            </div>
                            <div className="subready">
                                <span> 현재 준비 중입니다. </span>
                            </div>
                        </div>
                        );
        }
    } 


    modalSizeChange = () => {
        if(this.state.modalSizeClass === 'pop_maxsize_on')
            this.setState({...this.state,modalSizeClass:'pop_maxsize'})
        else
            this.setState({...this.state,modalSizeClass:'pop_maxsize_on'})
    }

    createModal = () => {
        const dragHandlers = {onStart: this.onStart, onStop: this.onStop};
        console.log("this.props.chosenFolder : ",this.props.chosenFolder);
        return (
            //   <DraggableCore onDrag={this.handleDrag} >
              <DraggableCore handle=".pop_head" {...dragHandlers} >
                <div className="modal moving" target="" 
                    onMouseDownCapture={ () => {this.selected(this.props.chosenFolder.index) } }
                    style={{ zIndex: this.props.chosenFolder.z_index }}
                    >
                    <div className="pop_head">    
                        <div className="pop_folder_img">
                            <input type="button" alt="" />
                        </div>
                        <div className="pop_title">{ this.props.chosenFolder.text }</div>
                        <div className="pop_mng">
                            <ul>
                                <li>
                                    <input type="button" className="pop_minsize"/>
                                </li>
                                <li onClick={() => {this.modalSizeChange()}}>
                                    <input type="button" className={this.state.modalSizeClass}/>
                                </li>
                                <li onClick={ () => { this.close(this.props.chosenFolder.index) } }>
                                    <input type="button" className="pop_close" />
                                </li>
                            </ul>
                        </div>
                    </div>
                    {/*
                    <nav>
                        <div>
                            <input className="pop_navi" type="text" alt="" />
                        </div>
                        <div>
                            <input className="pop_search" type="text" alt="" />
                        </div>
                    </nav> 
                    */}
                    { this.domContent(this.props.chosenFolder.name) }                    
                </div>
            </DraggableCore>
            )
    }
    
    render(){
        return(
            <div>
                {this.createModal()}
            </div>
        )   
    }
}

const mapToDispatch = (dispatch) =>({
     folderSelected: (index) => dispatch(actions.folderSelected(index)),
     folderClose   : (index) => dispatch(actions.folderClose(index)),
});

export default connect(null,mapToDispatch)(subWindow);